body {
  background-color:black;
  min-height: 100vh;
}

.black {
  min-height: 100px;
}

.left {
  display: flex;
  margin: auto;
  justify-content: space-between;
} 

#alec {
  max-width: 300px;
  margin-left: 6%;
}

#alec_au {
  margin-left: 8%;
}

#lec {
  margin-top: -2%;
  margin-right: 5%;
}

#arch {
  margin-top: 2%;
  margin-left: 2%;
}

#maya {
  position: fixed;
  bottom: 0;
  right: 0;
}

.center {
  color: red;
  text-align: center;
  min-width: 100%;
}

.nameWrapper {
  height: 50px;
  background-color: #FFFFFF!;
}



.name {
  font-size: calc(60px + 2vmin);
  font-weight: bold;
  font-family: 'Caveat', cursive;
  color: black;
  text-shadow:  -6px -2px red, 6px 2px #FFFFFF, 0 0 60px indianred;
  margin-left: 2%;
  min-width: 100%;
  position: fixed;
  bottom: 0;
  line-height: 0;
  font-family: 'Roboto Slab', serif;
}
